<template>
  <div class="login-modal solver-font">
    <h3 class="login-title"> ?</h3>
    <hr>
    <p>  . ?</p>
    <div class="login-button-bar">
      <button type="button" class="btn btn-submit" @click="goLogin"></button>
      <button type="button" class="btn btn-outline-cancel" @click="$emit('close')"></button>
    </div>
  </div>
</template>

<script>
export default {
  name: "LoginModal",
  methods : {
    goLogin : function(){
      this.$router.push({name: 'Login'})
      this.$emit('close')
    }
  }
}
</script>

<style>
  .login-modal{
     padding: 40px 50px;
  }

  .login-title{
    color: #0F4C81;
  }  

  .login-button-bar{
    text-align: center;
    margin-top: 30px;
  }

  .login-button-bar > button {
    margin: 0px 10px;
  }
</style>